.app-header {
    background-color: #282c34;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(4vmin);
    color: rgb(255, 255, 255);
    height: 10vh;
    width: 100%;    
    font-size: 4vh;
} 

.header-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: calc((100% - 10vh)/2);
}

.header-version {
  font-size: calc(2vmin);
}

.header-right {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: calc((100% - 10vh)/2);
    gap: 2vh;
    height: 100%;
}

.header-logo {
    color: #61dafb;
    height: 100%;
    width: 10vh;
  }

.header-logo-link {
    display: block;
    height: 100%;
    width: 100%;
  }

.header-logo-image {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  
@media (prefers-reduced-motion: no-preference) {
  .header-logo-image {
      animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
} 
 