 
.background-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

.button-text {
    position: relative;
    z-index: 1;
    color: white;
    font-size: 2vh; /* Adjust the font size of the text */
  }

  .custom-button {
    position: relative;
    display: block;
    width: 20vh;
    padding: 1vmin 2vmin;
    background-color: transparent;
    border: none;
    cursor: pointer;

    background-image: url('button_blue_n.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }  

  .custom-button:hover {
    background-image: url('button_blue_h.svg');
  }